$('.banner-container').slick({
  infinite: true,
  slidesToShow: 1,
  slidesToScroll: 1,
  autoplay: true,
  autoplaySpeed: 2000,
  arrows: true,
  dots: false
});

if (/Android|webOS|iPhone|iPad|iPod|BlackBerry/i.test(navigator.userAgent)) {
  $('.slick-mobile').slick({
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: true,
    dots: false
  });

  $('.menu-level-1 a').click(function () {
    var submenu = $(this).parent().find('.menu-level-2').length;
    if (submenu !== 0) {
      $(this).parent().find('.menu-level-2').toggleClass('show').slideToggle(500);
    }
  })
};

$('.burger-menu').click(function () {
  var isShow = $('.menu-list-container').hasClass('show');

  $('.menu-list-container').toggleClass('show');
  if(isShow) {
    $('.menu-level-2').slideUp('500');
  }
})
